    Scientific advances over the past 20 years have shown that drug addiction is a chronic, relapsing disease that results from the prolonged effects of drugs on the brain. As with many other brain diseases, addiction has embedded behavioral and social-context aspects that are important parts of the disorder itself. Therefore, the most effective treatment approaches will include biological, behavioral, and social-context components. Recognizing addiction as a chronic, relapsing brain disorder characterized by compulsive drug seeking and use can impact societys overall health and social policy strategies, reduce the stigma of addiction, and help diminish the health and social costs associated with drug abuse and addiction.\n            ")])])]),_c('div',{staticClass:"md-layout"},[_c('div',{staticClass:"md-layout-item md-size-66 md-xsmall-size-100 mx-auto text-center"},[_c('h3',{staticClass:"title text-center"},[_vm._v("WHAT CAUSES ADDICTION?")]),_c('h5',{staticClass:"description"},[_vm._v("\n              The word addiction is derived from a Latin term for enslaved by or bound to. Anyone who has struggled to overcome an addiction or has tried to help someone else to do so understands why. Addiction exerts a long and powerful influence on the brain that manifests in three distinct ways: craving for the object of addiction, loss of control over its use, and continuing involvement with it despite adverse consequences.\n\n              For many years, experts believed that only alcohol and powerful drugs could cause addiction. Neuroimaging technologies and more recent research, however, have shown that certain pleasurable activities, such as gambling, shopping, and sex, can also co-opt the brain.\n            ")])])]),_c('div',{staticClass:"md-layout"},[_c('div',{staticClass:"md-layout-item md-size-66 md-xsmall-size-100 mx-auto text-center"},[_c('h3',{staticClass:"title text-center"},[_vm._v("AVOIDING THE PAIN OF WITHDRAWAL")]),_c('h5',{staticClass:"description"},[_vm._v